@font-face {
  font-family: "robotoblack";
  src: url("../../assets/fonts/roboto-black-webfont.woff2") format("woff2"), url("../../assets/fonts/roboto-black-webfont.woff") format("woff"), url("../../assets/fonts/roboto-black-webfont.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "robotobold";
  src: url("../../assets/fonts/roboto-bold-webfont.woff2") format("woff2"), url("../../assets/fonts/roboto-bold-webfont.woff") format("woff"), url("../../assets/fonts/roboto-bold-webfont.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "robotoregular";
  src: url("../../assets/fonts/roboto-regular-webfont.woff2") format("woff2"), url("../../assets/fonts/roboto-regular-webfont.woff") format("woff"), url("../../assets/fonts/roboto-regular-webfont.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}
* {
  margin: 0;
  box-sizing: border-box;
}

html,
body {
  font-size: 10px;
  font-family: "robotoregular";
}

.header {
  height: 260px;
  width: 1336px;
  background-color: #000;
  display: flex;
  flex-direction: column;
}
@media (min-width: 767px) {
  .header {
    height: 136px;
    flex-direction: row;
  }
}
.header_logo {
  font-size: 7rem;
  color: #fff;
  font-family: "robotobold";
  line-height: normal;
  margin-left: 15.3rem;
  margin-top: 1.8rem;
}
@media (min-width: 767px) {
  .header_logo {
    font-size: 3.8rem;
  }
}
.header_content {
  display: flex;
}
.header_address {
  display: flex;
  margin-left: 8.3rem;
  margin-top: 2.8rem;
}
@media (min-width: 767px) {
  .header_address {
    margin-left: 15.3rem;
  }
}
.header_address-text {
  font-size: 3rem;
  color: #fff;
  font-family: "robotobold";
  width: 350px;
}
@media (min-width: 767px) {
  .header_address-text {
    font-size: 1.8rem;
  }
}
@media (min-width: 767px) {
  .header_address-text {
    width: 220px;
  }
}
.header_address-icon {
  height: 170px;
  width: 170px;
  margin-top: -70px;
  margin-right: -50px;
}
.header_phones {
  display: flex;
  margin-left: 4rem;
  margin-top: 2.8rem;
}
.header_phones-text > p {
  font-size: 3rem;
  color: #fff;
  font-family: "robotobold";
}
@media (min-width: 767px) {
  .header_phones-text > p {
    font-size: 1.8rem;
  }
}
.header_phones-icon {
  height: 90px;
  width: 90px;
  margin-top: -30px;
  margin-right: -10px;
}

.container {
  width: 100%;
  max-width: 136.6rem;
  margin: 0 auto;
}

body {
  background: #fff;
}

h1 {
  font-size: 7rem;
  color: #000;
  font-family: "robotobold";
  line-height: normal;
}
@media (min-width: 767px) {
  h1 {
    font-size: 3.8rem;
  }
}

p {
  font-family: "robotoregular";
}